/* ********TECH HEADING************ */
.tech__section-head {
    font-family: "Poppins Bold";
    font-size: 42px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.76;
    letter-spacing: normal;
    text-align: center;
    color: #252526;
    margin: 0;
}

.tech__section-head-text {
    padding: 0% 23.3%;
    font-family: "Poppins Regular";
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.75;
    letter-spacing: normal;
    text-align: center;
    color: rgba(33, 56, 66, 0.6);
}

.tech__heading {
     font-family: "Poppins Bold";
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    color: #252526;
}

.tech__text {
    font-family: "Poppins Regular";
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.6);
    letter-spacing: normal;
    text-align: justify;
}

.tech__row {
    margin-bottom: 5%;
}

.tech__image {
    object-fit: fill;
    max-height: 100%;
}
